.home {
  min-height: 100vh;
  background: #120b4f;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  font-size: 3rem;
  padding: 0;
}

.reggie-text-img {
  width: 90%;
  height: 100%;
  object-fit: cover;
  display: block;
  margin-top: 30px;
}

.three-box-row {
  width: 100%;
  display: flex;
  gap: 24px;
  margin-top: 32px;
  align-items: flex-start;
}

.three-box {
  flex: 1;
  height: 400px;
  border-radius: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  font-weight: 600;
  color: #fff;
}

.box1 {
  background: none;
  font-size: 30px;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: 10px;
  height: 400px;
  padding-left: 80px;
}

.box1-main {
  font-size: 30px;
  font-weight: 600;
  color: #fff;
  line-height: 1.2;
}

.box1-sub {
  color: #b0b0b0;
  font-size: 18px;
  font-weight: 400;
  margin-top: 0;
  line-height: 1.5;
}

.box2 {
  background: #f25c54;
  margin-top: -260px;
  border-radius: 50%;
  aspect-ratio: 1 / 1;
  width: 100px;
  height: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.box3 {
  background: none;
  position: relative;
}

.box3-btn {
  position: absolute;
  left: 50%;
  bottom: 32px;
  transform: translateX(-50%);
  border: 2px solid #fff;
  background: none;
  color: #fff;
  font-size: 18px;
  padding: 10px 32px;
  border-radius: 15px;
  cursor: pointer;
}

.work-section {
  width: 100%;
  display: flex;
  gap: 24px;
  margin-top: 48px;
  margin-bottom: 48px;
  justify-content: center;
}

.work-card {
  flex: 1;
  cursor: pointer;
  min-width: 220px;
  max-width: 340px;
  height: 450px;
  background: none;
  border: 2px solid #fff;
  border-radius: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  color: #fff;
  font-weight: 500;
  position: relative;
  overflow: hidden;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  transition: box-shadow 0.5s, transform 0.5s;
}

.work-card:hover {
  box-shadow: 0 8px 32px 0 #fff4, 0 2px 8px 0 #fff2;
  transform: translateZ(-8px) scale(1.03);
}

.work-card-img-container {
  width: 100%;
  height: 60%;
  padding: 12px;
  box-sizing: border-box;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  margin-top: 0;
}

.work-card-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 12px;
}

.work-card-content {
  width: 95%;
  padding: 0 18px;
  margin-top: 12px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  word-break: break-word;
  overflow-wrap: break-word;
}

.work-card-title {
  font-weight: 600;
  color: #fff;
  margin-bottom: 6px;
  margin-top: 0;
}

.work-card-desc {
  color: #b0b0b0;
  font-size: 16px;
  font-weight: 400;
  margin: 0;
  line-height: 1;
}

.work-card-year {
  position: absolute;
  right: 18px;
  bottom: 14px;
  color: #ffffff80; 
  font-size: 50px;
  font-style: italic;
  font-weight: 700;
  letter-spacing: 1px;
  pointer-events: none;
}